import React from "react";
import "./Header.css";

function Header(props) {
  return (
    <header className={"header" + " " + props.className}>
      <img
        src="./images/sven-scheuermeier-VNseEaTt9w4-unsplash.jpg"
        alt="background_image"
        className="background_image"
      />
      <img
        src="./images/Lisandro_resalta_print_Seeber_DSC4065-2-2.jpg"
        alt="personal_image"
        className="image"
      ></img>
      <h1 className="presentation_text">
        <strong>Hi, I'm Lisandro Cesaratto,</strong>
        <br />
        an Electronic Engineer,
        <br />
        currently studying a M.Sc. in CS.
        <br />
      </h1>
    </header>
  );
}

export default Header;
